import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';

@Component({
    selector: 'app-using-documents',
    templateUrl: './using-documents.component.html',
    styleUrls: ['./using-documents.component.scss']
})
export class UsingDocumentsComponent implements OnInit {
    paramsList: Array<any> = [];

    constructor(
        public location: Location
    ) { }

    ngOnInit() {
        this.paramsList = [
            {
                name: 'tabsData',
                explain: 'tabs',
                type: `Array<{text:String}>textdropdown:true
                    dropdownArray:Array<{text:String}>,disabledtrue`,
                optionaValue: '',
                defaultValue: '-'
            },
            {
                name: 'tabsType',
                explain: 'tabs',
                type: 'String',
                optionaValue: 'line-tabsbootstrap-line-tabsbootstrap-btn-tabs',
                defaultValue: 'line-tabs'
            },
            {
                name: 'bootomBorder2X',
                explain: 'line-tabs',
                type: 'Boolean',
                optionaValue: 'truefalse',
                defaultValue: 'false'
            },
            {
                name: 'colorThemeStyle',
                explain: 'line-tabsitem',
                type: 'String',
                optionaValue: '',
                defaultValue: 'm-tabs-line--primary'
            },
            {
                name: 'btnColor',
                explain: 'bootstrap-btn-tabsbtn',
                type: 'String',
                optionaValue: '',
                defaultValue: 'nav-pills--success'
            },
            {
                name: 'iconColor',
                explain: 'bootstrap-tabsicon',
                type: 'String',
                optionaValue: '',
                defaultValue: 'm--font-brand'
            },
            {
                name: 'choice',
                explain: 'tab',
                type: 'Number',
                optionaValue: 'tab',
                defaultValue: '0tab'
            },
            {
                name: 'selected',
                explain: '',
                type: '',
                optionaValue: '',
                defaultValue: '-'
            }
        ];
    };
};



